import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { AdminContext } from '../App';

const Navigation = () => {
    const { loginLogout } = useContext(AdminContext);

    return (
        <div className='w-full h-16 static top-0 bg-slate-900 text-white flex justify-between items-center px-2 md:px-6'>
            <div>
                <Link to={`/`} className=''>
                    <h3 className='text-2xl font-semibold hover:underline transition-all cursor-pointer' style={{ fontFamily: 'cursive' }}>Survey Form</h3>
                </Link>
            </div>
            {loginLogout ?
                <div className='flex gap-2'>
                    <Link to={`/survey/preview`} className='py-2 px-4 hover:border-y shadow-sm rounded-md'>View Survey</Link>
                    <Link to={`/admin/logout`} className='py-2 px-4 bg-sky-600 rounded-md'> Logout</Link>
                </div>
                :
                <div className='flex gap-2'>
                    <Link to={`/survey/form`} className='py-2 px-4 hover:border-y shadow-sm rounded-md'>Find Survey</Link>
                    <Link to={`/admin/login`} className='py-2 px-4 bg-sky-600 rounded-md'>Login</Link>
                </div>

            }
        </div>
    )
}

export default Navigation