import React, {useEffect, useState, useContext} from 'react'
import handelDataFetch from '../Controller/handelDataFetch'
import { useNavigate } from 'react-router-dom';
import { AdminContext } from '../App';

const Logout = () => {

    const [isLogout, setIsLogout] = useState({
        status: false,
        message: ''
    });

    const navigate = useNavigate();
    const { setLoginLogout } = useContext(AdminContext);

    const logout = async() => {
        const result = await handelDataFetch('/admin/logout','GET');
        if(result.status) {
            setIsLogout({
                status: true,
                message: result.message
            });

            setLoginLogout({ type: "ADMIN", payload: false });
        } 
        navigate('/admin/login');
    }

    useEffect(() => {
        logout();
    },[])

  return (
    <div className='flex justify-center items-center h-screen bg-sky-100'>
            <p className='text-center'>
                <h1 className='text-5xl'>{isLogout.status && isLogout.message}</h1>
            </p>
        </div>
  )
}

export default Logout