import { Route, Routes } from "react-router-dom";
import { createContext, useEffect, useReducer } from "react";
import handelDataFetch from '../src/Controller/handelDataFetch';
import SurveyForm from "./Components/SurveyForm";
import SurveyFormEdit from "./Components/SurveyFormEdit";
import PageNotFound from "./Components/PageNotFound";
import Login from "./Components/Login";
import SurveyPreviewTable from "./Components/SurveyPreviewTable";
import Navigation from "./Components/Navigation";


import { initialState, reducer } from './reducer/Reducer';
import Logout from "./Components/Logout";
import Signup from "./Components/Signup";

export const AdminContext = createContext();




function App() {
  const [loginLogout, setLoginLogout] = useReducer(reducer, initialState);


  const handelIsLogin = async () => {
    const result = await handelDataFetch('/api/v1/authentication', 'GET');

    if (result.status) {
      setLoginLogout({ type: "ADMIN", payload: true });
    } else {
      setLoginLogout({ type: "ADMIN", payload: false });
    }
  }

  useEffect(() => {
    handelIsLogin();
  }, []);

  return (
    <>
      <AdminContext.Provider value={{ loginLogout, setLoginLogout }}>
        <Navigation />

        <Routes>
          <Route exact path="/" element={<Login />} />
          <Route exact path="/admin/login" element={<Login />} />
          <Route exact path="/admin/signup" element={<Signup />} />
          <Route exact path="/admin/logout" element={<Logout />} />
          <Route exact path="/survey/form" element={<SurveyForm />} />
          <Route exact path="/survey/form/edit/:id" element={<SurveyFormEdit />} />
          <Route exact path="/survey/preview" element={<SurveyPreviewTable />} />


          <Route path="*" element={<PageNotFound />} />
        </Routes>

      </AdminContext.Provider >
    </>
  );
}

export default App;
