import React, { useEffect, useState, useContext } from 'react';
import { Table, Space, message } from 'antd';
import handelDataFetch from '../Controller/handelDataFetch';
import { AdminContext } from '../App';
import { Link, useNavigate } from 'react-router-dom';

const SurveyTable = () => {
    const [dataSource, setDataSource] = useState(null);

    const navigate = useNavigate();
    const { setLoginLogout } = useContext(AdminContext);

    const handelGetData = async () => {
        const result = await handelDataFetch('/api/v1/survey-form', 'GET');
        if (result.status) {
            setDataSource(result.result);
        } else {
            setLoginLogout({ type: "ADMIN", payload: false });
            navigate('/admin/login');
        }
    }

    useEffect(() => {
        handelGetData();
    }, [])


    const handleDeleteSurvey = async (_id) => {
        const result = await handelDataFetch(`/api/v1/survey-form/${_id}`, 'DELETE');
        console.log(result);
        if(result.status) {
            message.success(result.message);
            handelGetData();
        } else {
            message.error(result.message);
        }
    }


    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
        },
        {
            title: 'Email',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: 'Phone Number',
            dataIndex: 'phone',
            key: 'phone',
        },
        {
            title: "Gender",
            dataIndex: 'gender',
            key: 'gender',
        },
        {
            title: 'Nationality',
            dataIndex: 'nationality',
            key: 'nationality',
        },
        {
            title: 'Address',
            dataIndex: 'address',
            key: 'address',
        },
        {
            title: 'Message',
            dataIndex: 'message',
            key: 'message',
        },
        {
            title: 'Action',
            dataIndex: '_id',
            key: '_id',
            render: (_id) => {
                return (
                    <div className='flex flex-col'>
                        <Space size={'small'} className='mb-1'>
                            <Link to={`/survey/form/edit/${_id}`} className='bg-blue-700 py-2 px-5 rounded-md my-1 text-white hover:bg-blue-600 w-16'> <span>Edit</span></Link>
                        </Space>
                        <Space size={'small'} className='mb-1'>
                            <button className='bg-red-600 py-1 rounded-md my-1 text-white hover:bg-red-500 w-16' onClick={() => {handleDeleteSurvey(_id)}}> <span>Delete</span></button>
                        </Space>
                    </div>

                )
            },
            width: 150
        },
    ];

    return (
        <>
            <Table
                dataSource={dataSource}
                columns={columns}
                pagination={{
                    position: ['bottomCenter'],
                    pageSize: 20,
                }}
                className='overflow-x-auto'
            />
        </>
    )
}

export default SurveyTable