import React, { useState } from 'react';
import nationality from '../api/nationality';
import handelDataFetch from '../Controller/handelDataFetch';
import { Modal, message } from 'antd';

import successImg from '../images/success.webp';

const SurveyForm = () => {

  const init = {
    firstName: '',
    lastName: '',
    name: '',
    email: "",
    phone: '',
    gender: "",
    nationality: "none",
    address: "",
    message: ""
  };

  const [survey, setSurvey] = useState(init);
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);

  let name, value;
  const handleInput = (e) => {
    name = e.target.name;
    value = e.target.value

    setSurvey({ ...survey, [name]: value });
  }

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (survey.firstName === '' || survey.lastName === '' || survey.email === '' || survey.gender === '' || survey.phone === '' || survey.nationality === '' || survey.address === '' || survey.message === '') {
      message.error("All Field is required");
    } else {
      survey.name = survey.firstName + " " + survey.lastName;

      const result = await handelDataFetch('/api/v1/survey-form', "POST", survey);

      if (result.status) {
        message.success(result.message);
        setIsFormSubmitted(true);
      } else {
        message.error(result.message);
        setIsFormSubmitted(false);
      }
    }
  }

  const handelCancelModel = () => {
    window.location.reload();
  }

  const nationalitySelect = nationality.map((elem, index) => {
    return <option key={index} value={elem}>{elem}</option>;
  })

  return (
    <>
      <div className='w-full min-h-screen flex justify-center items-start md:pt-16 bg-red-300'>
        <div className="rounded-md w-full md:w-2/3 xl:w-2/5 bg-slate-50 shadow-md">
          <div className="bg-rose-950 flex justify-center items-center p-3 md:p-5 md:rounded-sm">
            <h1 className="text-3xl font-medium text-white">Survey Form</h1>
          </div>
          <div className="bg-white p-1 md:p-4 flex flex-col">
            <div className='flex flex-col mb-3 p-2 md:p-4 border '>
              <h4 className='text-xl mb-2 ms-1'>Your Name</h4>
              <div className="flex flex-col md:flex-row gap-2 w-full">
                <div className='flex flex-col w-full'>
                  <input type="text" name="firstName" id="firstName" className='p-2 rounded-md bg-yellow-100 text-black focus:bg-yellow-50 transition-all' required onChange={handleInput} value={survey.firstName}/>
                  <label htmlFor="firstName" className='m-1 text-xs'>First Name </label>
                </div>
                <div className='flex flex-col w-full'>
                  <input type="text" name="lastName" id="lastName" className='p-2 rounded-md bg-yellow-100 text-black focus:bg-yellow-50' required onChange={handleInput} value={survey.lastName}/>
                  <label htmlFor="lastName" className='m-1 text-xs'>Last Name </label>
                </div>
              </div>
            </div>
            <div className='flex flex-col mb-3 p-2 md:p-4 border'>
              <h4 className='text-xl mb-2 ms-1'>Your Email</h4>
              <div className="flex flex-col w-full">
                <input type="email" name="email" id="email" className='p-2 rounded-md bg-yellow-100 focus:bg-yellow-50' required onChange={handleInput} value={survey.email}/>
                <label htmlFor="email" className='m-1 text-xs'>example@example.com </label>
              </div>
            </div>
            <div className='flex flex-col mb-3 p-2 md:p-4 border'>
              <h4 className='text-xl mb-2 ms-1'>Your Phone</h4>
              <div className="flex flex-col w-full">
                <input type="tel" name="phone" id="phone" className='p-2 rounded-md bg-yellow-100 focus:bg-yellow-50' pattern="[0-9]*" required onChange={handleInput} value={survey.phone}/>
                <label htmlFor="phone" className='m-1 text-xs'>Example: 987654210 </label>
              </div>
            </div>
            <div className="flex flex-col mb-3 p-2 md:p-4 border">
              <h4 className='text-xl mb-2 ms-1'>Gender</h4>
              <div className="ms-3">
                <input type="radio" name="gender" id="male" value="male" required onChange={handleInput} checked={survey.gender === 'male' && true} />
                <label htmlFor="male" className='ms-2'> Male</label>
              </div>
              <div className="ms-3">
                <input type="radio" name="gender" id="female" value="female" required onChange={handleInput} checked={survey.gender === 'female' && true}/>
                <label htmlFor="female" className='ms-2'> Female</label>
              </div>
              <div className="ms-3">
                <input type="radio" name="gender" id="other" value="other" required onChange={handleInput} checked={survey.gender === 'other' && true}/>
                <label htmlFor="other" className='ms-2'> Other</label>
              </div>
            </div>
            <div className="flex flex-col mb-3 p-2 md:p-4 border">
              <h4 className='text-xl mb-2 ms-1'>Nationality</h4>
              <div className="flex flex-col w-full">
                <select defaultValue={survey.nationality === 'none'? 'none' :  survey.nationality} name="nationality" id="nationality" className='w-full bg-yellow-100 p-2 rounded-md focus:bg-yellow-50' required onChange={handleInput} >
                  <option value="none" disabled>--Select Nationality--</option>
                  {nationalitySelect}
                </select>
                <label htmlFor="nationality" className='m-1 text-xs'>Select your country</label>
              </div>
            </div>
            <div className="flex flex-col mb-3 p-2 md:p-4 border">
              <h4 className='text-xl mb-2 ms-1'>Address</h4>
              <div className="flex flex-col w-full">
                <textarea name="address" id="address" cols="30" rows="5" className='bg-yellow-100 p-2 rounded-md focus:bg-yellow-50' onChange={handleInput} value={survey.address}></textarea>
                <label htmlFor="address" className='m-1 text-xs'>Example: house no, street, city, state, country, postal code. </label>
              </div>
            </div>
            <div className="flex flex-col mb-3 p-2 md:p-4 border">
              <h4 className='text-xl mb-2 ms-1'>Message</h4>
              <div className="flex flex-col w-full">
                <textarea name="message" id="message" cols="30" rows="5" className='bg-yellow-100 p-2 rounded-md focus:bg-yellow-50' onChange={handleInput} value={survey.message}></textarea>
                <label htmlFor="message" className='m-1 text-xs'>Example: Give your feedback to us. </label>
              </div>
            </div>
            <div className="flex justify-start p-5">
              <button type="submit" className='bg-green-400 py-2 px-4 text-2xl rounded-md hover:bg-green-300' onClick={handleSubmit}>Submit</button>
            </div>
          </div>
        </div>
      </div>
        <Modal title="Successfully Collected Your Data" open={isFormSubmitted} footer={null} onCancel={handelCancelModel} >
            <div className='flex items-center'>
              <img src={successImg} alt="Successfully Submitted." className='w-20'/>
              <h3 className='text-xl ms-3'>Thank you, for you feedback.</h3>
            </div>
        </Modal>

    </>
  )
}

export default SurveyForm