import React, { useState, useContext, useEffect } from 'react'
import handelDataFetch from '../Controller/handelDataFetch';
import { useNavigate, Link } from 'react-router-dom';
import { AdminContext } from '../App';

const Signup = () => {
    document.title = "Signup";

    const [admin, setAdmin] = useState({
        userName: '',
        password: '',
    });

    const init = {
        status: false,
        type: 'Error',
        message: ""
    }

    const [validation, setValidation] = useState(init);


    const { setLoginLogout } = useContext(AdminContext);

    const navigate = useNavigate();

    const handelIsLogin = async () => {
        const result = await handelDataFetch('/api/v1/authentication','GET');

        if(result.status) {
            navigate('/survey/preview');
            setLoginLogout({ type: "ADMIN", payload: true });
        }
    }

    useEffect(() => {
        handelIsLogin();
    }, [])

    // methods
    const handleInput = (e) => {
        if (e.target.name === 'userName') {
            setAdmin({ ...admin, userName: e.target.value });
        } else {
            setAdmin({ ...admin, password: e.target.value });
        }
    }

    const handleSubmit = async (e) => {
        setValidation(init);
        e.preventDefault();

        if (admin.userName === '' || admin.password === '') {
            setValidation({
                status: true,
                type: 'Error',
                message: "All Filed is Required!..."
            });
        } else {
            const result = await handelDataFetch('/admin/signup', 'POST', admin);

            if (result.status) {
                setValidation({
                    status: true,
                    type: 'Success',
                    message: result.message
                });
                setLoginLogout({ type: "ADMIN", payload: true });
                setTimeout(() => {
                    navigate('/survey/preview')
                },1000);
            } else {
                setValidation({
                    status: true,
                    type: 'Error',
                    message: result.message
                });
            }
        }

    }

    return (
        <div className='flex justify-center items-start pt-5 md:pt-16 bg-sky-200 h-screen'>
            <div className="border border-slate-600 rounded-md p-3 md:p-8 w-full md:w-2/3 xl:w-2/5 bg-slate-900 text-white shadow-xl">
                <div className="flex flex-col">
                    <h3 className='text-2xl text-center w-full mb-8'>Create New Admin Account</h3>
                    {validation.status &&
                        <p className={`text-center ${validation.type === 'Error' ? 'text-red-300' : 'text-green-300'} font-mono`}>{validation.message}</p>
                    }
                    <div className='flex flex-col mb-3'>
                        <label htmlFor="userName" className='ms-1 mb-1'>UserName: </label>
                        <input type="text" name="userName" id="userName" placeholder='Enter User Name' className='p-2 rounded-md text-black' onChange={handleInput} />
                    </div>
                    <div className='flex flex-col mb-3'>
                        <label htmlFor="password" className='ms-1 mb-1'>Password: </label>
                        <input type="password" name="password" id="password" placeholder='Enter Password' className='p-2 rounded-md text-black' onChange={handleInput} />
                    </div>
                    <div className='mb-3'>
                        Already Having Account? <Link to={`/admin/login`} className='text-blue-500 hover:underline'>Sign-in</Link>
                    </div>
                    <div className='ms-1'>
                        <button type="submit" className='mt-2 p-3 bg-green-600 hover:bg-green-500 transition-all rounded-xl' onClick={handleSubmit}>Sign-up</button>
                    </div>

                </div>
            </div>
        </div>
    )
}

export default Signup